<template>
    <div class="kingKongList">
        <ul>
            <li v-for="(itme,index) in list" :key='itme.sequen'  @click='chanActon(index,urlList,itme.schemeUrl)'>
                <img v-lazy="itme.picUrl" alt="">
                <div class="text">{{itme.text}}</div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name:'kingKong-List',
    props:{
        list:{
            type:Array,
            required:true,
        },
        urlList:String,
    },
    methods:{
        chanActon(index,url,schemeUrl){
            if(schemeUrl.indexOf('?') < 0){
                return false;
            }
            let id = schemeUrl.split('?')[1].split('&')[0].split('=')[1];
            this.$emit('click-Navigation',url,id)
             this.$store.commit('Home/sethomeNavIndex',index);
        }
    },
}
</script>

<style scoped>
.kingKongList{
    width: 100%;
    background: #fff;
    font-size: 12px;
    text-align: center;
    margin-bottom: 10px;
    padding-bottom: 10px;
}
li{
    display: inline-block;
    width: 20%;
    padding: 5px;
    box-sizing: border-box;
    flex-direction: column;
}
img{
    width: 100%;
}
.text{
    width: 100%;
}

</style>
