<template>
    <div Id='channelAn' @click='chanActon()'>
        <ul>
            <li v-for="(itme,index) in list"
            @click='chanActon(index,urlList,itme.id)'
            :class="{Action:index == $store.state.Home.homeNavIndex}"
            :key='itme.id'
            >{{itme.name}}</li>
        </ul>
    </div>
</template>

<script>
export default {
    name:'channelAn-itme',
    props:{
        list:{
            type:Array,
            required:true
        },
         urlList:{
            type:String,
        },
    },
    methods:{
        chanActon(index,url,id){
            if(index != undefined){
                this.$store.commit('Home/sethomeNavIndex',index);
            }
            if(id != -1){
                this.$emit('click-show',url,id);
            }
            
        }
    }
}
</script>

<style lang='scss' scoped>
#channelAn{
    position:fixed;
    top:74px;
    bottom: 0;
    z-index: 10;
    background: rgba(0,0,0,0.6)
}

ul{
    padding-top: 20px;
    background: #fff;
    overflow: hidden;
    z-index: 1;
    position: relative;
    top:-1px;
}
li{
    float: left;
    margin-left:15px;
    margin-bottom: 20px;
    width: 70px;
    line-height: 24px;
    text-align: center;
    border: 1px solid #666;
}
.Action{
    border: 1px solid #b4282d;
    color: #b4282d;
}
</style>
