<template>
    <div>
        <!-- 登录搜索栏 -->
        <search-itme></search-itme>
        <!-- 导航栏 -->
        <div id='home-nav-warp'>
            <Transever :urlList='urlLists' :list='homeNavList' :homeNavIndex='homeNavIndex' @click-Navigation="navigationAction" v-show="!channelAnShow"></Transever>
            <div v-show="channelAnShow" class="nav-an-show">全部频道</div>
            <div class="nav-an" :class="{action:channelAnShow}" @click="channelAnAction()"></div>
        </div>
        <!-- 导航栏展开 -->
        <channelAn-itme v-show="channelAnShow" @click-show='navigationAction' :urlList='urlLists' :list='homeNavList' ></channelAn-itme>
        <!-- 导航栏分页面,初始为隐藏 -->
        <router-view></router-view>
        <!------- 主体信息部分,初始为显示 ------->
        <srcoll-itme id='home'  v-if="homeNavIndex==0" class="conten">
            <!-- 轮播图 -->
            <banner-li :list='bannerList'></banner-li>
            <!-- 网易自营品牌 -->
            <grow-li :list='policyDescList'></grow-li>
            <!-- 新品首发展示 -->
            <kingKong-List :list='kingKongList' :urlList='urlLists' @click-Navigation="navigationAction"></kingKong-List>
            <!-- 海外商品 -->
            <htag-List :list='homeTagList'></htag-List>
            <!-- 类目热销榜 -->
            <categoryList :list='categoryList'></categoryList>
            <!-- 人气推荐 -->
            <popularItemList :list='popularItemList'></popularItemList>
            <!-- 限时购 -->
            <itemList :list='itemList'></itemList>
            <!-- 新品首发列表 -->
            <newItemList :list='newItemList'></newItemList>
            <!-- 99超值专场 -->
            <sceneLightShoppingGuideModule :list='sceneLightShoppingGuideModule'></sceneLightShoppingGuideModule>
            <!-- 底部展示分类 -->
            <categoryModule v-for="num in categoryModule.length" :key='num' :list='categoryModule[num-1]'></categoryModule>
        </srcoll-itme>
    </div>
</template>



<script>
import scroll from '../../../components/scroll';
import transverse from '../../../components/transverse';
import channelAn from './childre/channelAn';
import homeAxios from '../../../data/home';
import Banner from '../../../components/banner';
import search from './childre/search';
import Grow from './childre/g-grow';
import kingKongList from './childre/kingKongList';
import htagList from './childre/htagList';
import categoryList from './childre/categoryList';
import popularItemList from './childre/popularItemList';
import itemList from './childre/itemList';
import newItemList from './childre/newItemList';
import sceneLightShoppingGuideModule from './childre/sceneLightShoppingGuideModule';
import categoryModule from './childre/categoryModule';
//import navigation from './childre/navigation';


import BScroll from 'better-scroll';
import { mapState , mapActions } from 'vuex';
export default {
    components:{
        [scroll.name]:scroll,
        [search.name]:search,
        [transverse.name]:transverse,
        [channelAn.name]:channelAn,
        [Banner.name]:Banner,
        [Grow.name]:Grow,
        [kingKongList.name]:kingKongList,        
        [htagList.name]:htagList,  
        categoryList,   
        popularItemList,  
        itemList,
        newItemList, 
        sceneLightShoppingGuideModule,
        categoryModule,
        // navigation
    },
    data:function(){
        return {
            channelAnShow:false,
            urlLists:'/api/home/categoryItemList',
        }
    },
    methods:{
         ...mapActions({
            //获取homeVuex请求数据的方法
            requesHomeNavList:'Home/requesHomeNavList',
            requesBannerList:'Home/requesBannerList',
            requesPolicyDescList:'Home/requesPolicyDescList',
            requesKingKongList:'Home/requesKingKongList',
            requesHomeTag:'Home/requesHomeTag',
            requesCategoryList:'Home/requesCategoryList',
            requesPopularItemList:'Home/requesPopularItemList',
            requesItemList:'Home/requesItemList',
            requesNewItemList:'Home/requesNewItemList',
            requesSceneLightShoppingGuideModule:'Home/requesSceneLightShoppingGuideModule',
            requesCategoryModule:'Home/requesCategoryModule',
            requesHomeNavIgaTion:'Home/requesHomeNavIgaTion',

            
         }),
        //控制蒙版层显示隐藏
        channelAnAction(){
            this.channelAnShow = !this.channelAnShow;
        },
        //请求导航分页数据
        navigationAction(url,id){
            if(url,id){
                this.requesHomeNavIgaTion([url,id]);
            }
            this.channelAnShow = false;
            this.$router.push('/homeNav/'+ id);
        },
        
        
    },
    computed:{
        ...mapState({
            //获取homeVuex的sdate数据
            homeNavList: state=> state.Home.homeNavList,
            bannerList:state=> state.Home.bannerList,
            policyDescList:state=> state.Home.policyDescList,
            kingKongList:state=> state.Home.kingKongList,
            homeTagList:state=> state.Home.homeTagList,
            categoryList:state=> state.Home.categoryList,
            popularItemList:state=> state.Home.popularItemList,
            itemList:state=> state.Home.itemList,
            homeNavIndex:state=> state.Home.homeNavIndex,
            newItemList:state=> state.Home.newItemList,
            sceneLightShoppingGuideModule:state=> state.Home.sceneLightShoppingGuideModule,
            categoryModule:state=> state.Home.categoryModule,
            homeNavIgaTion:state=> state.Home.homeNavIgaTion,

        }),
    },
    created(){
        //请求头部导航栏信息
        this.requesHomeNavList('/api/home/homeNav');
        //请求轮播图
        this.requesBannerList('/api/home/homeBanner');
        //请求网易自营品牌
        this.requesPolicyDescList('/api/home/policyDescList');
        //请求首页新品首发
        this.requesKingKongList('/api/home/kingKongList');
        //请求海外商品
        this.requesHomeTag('/api/home/homeTag');
        //请求类目热销
        this.requesCategoryList('/api/home/categoryList');
        //请求人气推荐
        this.requesPopularItemList('/api/home/popularItemList');
        //请求限时购
        this.requesItemList('/api/home/itemList');
        //请求新品首发列表
        this.requesNewItemList('/api/home/newItemList');
        //请求99超值专场
        this.requesSceneLightShoppingGuideModule('/api/home/sceneLightShoppingGuideModule');
        //请求底部展示分类
        this.requesCategoryModule('/api/home/categoryModule');
    },
    beforeRouteUpdate (to, from, next) {
        if(to.fullPath=='/'){
            this.$store.commit('Home/sethomeNavIndex',0);
        }
        next();
    },
    beforeRouteLeave  (to, from, next) {
        this.$store.commit('Home/sethomeNavIndex',0);
        next();
    },
}
</script>

<style lang='scss' scoped>

#home-nav-warp{
    width: 100%;
    background: #fff;
    position: relative;
    display: flex;
    line-height: 30px;
    position: fixed;
    left: 0;
    top:44px;
    .nav-an{
        display: block;
        margin: 6px 18px 0 34px;
        width: 14px;
        height: 14px;
        background: url('/images/navIcon.png') no-repeat;
        background-size: 100%; 
        transition: 200ms;
        &.action{
            transform: rotate(180deg);
        }
    }
    .nav-an-show{
        flex: 1;
        padding-left: 15px;
    }
    
}
</style>
