<template>
    
    <div class="home-nav" >
        <ul>
            <li v-for="(itme,index) in list"
            @click='chanActon(index,urlList,itme.id)'
            :id='itme.id'
            :class="{Action:index == homeNavIndex}"
            :key='itme.id'
            >{{itme.name}}</li>
        </ul>
    </div>
</template>

<script>
import BScroll from 'better-scroll';
export default {
    name:'Transever',
    props:{
        list:{
            type:Array,
            required:true
        },
        urlList:String,
        homeNavIndex:Number,
    },
    data:function(){
        return {
            Scrolls:'',
        }
    } ,
    watch:{
        homeNavIndex:function(){
            let i = this.homeNavIndex;
            if(this.list.length==0){
                return false;
            }
            let id =  this.list[i].id;
            var el = document.getElementById(id);
            this.Scrolls.scrollToElement(el,200,true);
        }
    },
    methods:{
        chanActon(index,url,id){
            if(id != -1){
                this.$emit('click-Navigation',url,id)
            }
            this.$store.commit('Home/sethomeNavIndex',index);
        }
    },
    mounted(){
        this.Scrolls = new BScroll('.home-nav',{
            scrollX: true,
            click: true,
            tap:true
        })
    }
}
</script>

<style lang="scss" scoped>
.home-nav{
    line-height: 100%;
    height: 35px;
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    ul{
        display: inline-block;
        height: 30px;
        position: relative;
        li{
            height: 100%;
            padding: 0 13px;
            line-height: 100%;
            box-sizing: border-box;
            display: inline-block;
            border-bottom: 2px solid transparent;
        }
        .Action{
            border-bottom: 2px solid #b4282d;
            color: #b4282d;
        }
    }
    
}
</style>

